<template>
  <div class="flex-flex">
    <m-btn v-for="(item,index) in dataList" :sizeh="30" :class="btnIndex==index?'select-active':''" @click.native="btnIndexChange(index)"></m-btn>
  </div>
</template>

<script>
    export default {
      name: 'TabSecond',
      prop: ['dataList'],
      data: () => ({
        list: [
          {name: ''},
          {name: ''},
          {name: ''}
        ],
        btnIndex: 0
      }),
      methods: {
        btnIndexChange (index) {
          this.btnIndex = index
        }
      }
    }
</script>

<style lang='scss'>
</style>



